.loader {
    width: 80px;
    height: 80px;
    position: fixed;
    left: 50%;
    top: 50%;
    margin: -40px 0 0 -40px;
    z-index: 100;
    background: rgba(0, 0, 0, 0.6);
    visibility: hidden;
    opacity: 0;
    -moz-transition: opacity .2s linear, visibility 0s linear .2s;
    -o-transition: opacity .2s linear, visibility 0s linear .2s;
    -webkit-transition: opacity .2s linear, visibility 0s linear;
    -webkit-transition-delay: 0s, .2s;
    transition: opacity .2s linear, visibility 0s linear .2s
}

.loader-spin {
    width: 15px;
    height: 30px;
    position: absolute;
    right: 40px;
    top: 25px;
    overflow: hidden;
    -webkit-animation: loader 1.2s infinite;
    -moz-animation: loader 1.2s infinite;
    -o-animation: loader 1.2s infinite;
    animation: loader 1.2s infinite;
    animation-timing-function: linear;
    -webkit-animation-timing-function: linear;
    -moz-animation-timing-function: linear;
    -webkit-transform-origin: 100% 50%;
    -moz-transform-origin: 100% 50%;
    -o-transform-origin: 100% 50%;
    transform-origin: 100% 50%;
    -webkit-animation-play-state: paused;
    animation-play-state: paused
}

.loader-spin span {
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 22px;
    height: 22px;
    border: 4px solid #fff;
    -moz-border-radius: 20px;
    -webkit-border-radius: 20px;
    border-radius: 20px
}

.is-doing-request .loader,
.is-loading-content .loader {
    visibility: visible;
    opacity: 1;
    -moz-transition: opacity .3s linear .4s, visibility 0s linear 0s;
    -o-transition: opacity .3s linear .4s, visibility 0s linear 0s;
    -webkit-transition: opacity .3s linear, visibility 0s linear;
    -webkit-transition-delay: .4s, 0s;
    transition: opacity .3s linear .4s, visibility 0s linear 0s
}

.is-doing-request .loader-spin,
.is-loading-content .loader-spin {
    -webkit-animation-play-state: running;
    animation-play-state: running
}

@-webkit-keyframes loader {
    0% {
        -webkit-transform: rotate(0deg)
    }
    100% {
        -webkit-transform: rotate(360deg)
    }
}

@-moz-keyframes loader {
    0% {
        -moz-transform: rotate(0deg)
    }
    100% {
        -moz-transform: rotate(360deg)
    }
}

